'use client';

import { useState, useEffect, useRef } from 'react';
import { Card, CardHeader, CardTitle, CardContent } from '@/components/ui/card';
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from '@/components/ui/table';
import { getRecentTradesData } from '@/lib/actions';
import { type RecentTrade } from '@/lib/bybit-api';
import { ScrollArea } from '@/components/ui/scroll-area';
import { Skeleton } from '@/components/ui/skeleton';
import { format } from 'date-fns';
import { cn } from '@/lib/utils';

const formatTime = (timestamp: string | number | undefined): string => {
  if (timestamp === undefined || timestamp === null) {
    return '...';
  }
  try {
    const numericTimestamp = Number(timestamp);
    if (isNaN(numericTimestamp) || !isFinite(numericTimestamp) || numericTimestamp <= 0) {
      return 'Invalid Time';
    }
    return format(new Date(numericTimestamp), 'HH:mm:ss');
  } catch (e) {
    console.error("Error formatting time:", e);
    return 'Invalid Time';
  }
};

const formatAmount = (amountStr: string | undefined): string => {
    if (!amountStr) return '0.000';
    const amount = parseFloat(amountStr);
    if (isNaN(amount)) return '0.000';

    if (amount >= 1000) {
        return `${(amount / 1000).toFixed(2)}K`;
    }
    if (amount < 0.01 && amount > 0) {
      return amount.toFixed(5);
    }
    return amount.toFixed(3);
}

const LoadingRows = () => (
  Array.from({ length: 15 }).map((_, i) => (
      <TableRow key={i} className="text-xs">
          <TableCell className="p-2"><Skeleton className="h-4 w-full" /></TableCell>
          <TableCell className="p-2"><Skeleton className="h-4 w-full" /></TableCell>
          <TableCell className="p-2"><Skeleton className="h-4 w-full" /></TableCell>
      </TableRow>
  ))
);

interface RecentTradesProps {
  symbol: string;
}

export function RecentTrades({ symbol }: RecentTradesProps) {
  const [trades, setTrades] = useState<RecentTrade[]>([]);
  const [newTradeIds, setNewTradeIds] = useState<Set<string>>(new Set());
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const prevTradeIds = useRef<Set<string>>(new Set());

  useEffect(() => {
    const fetchData = async (isInitialLoad = false) => {
      if (isInitialLoad) {
        setIsLoading(true);
        setTrades([]);
        prevTradeIds.current.clear();
        setError(null);
      }
      
      try {
        const recentTrades = await getRecentTradesData(symbol);
        if (Array.isArray(recentTrades)) {
            
          const newTradesFound = recentTrades.filter(t => !prevTradeIds.current.has(t.execId) && prevTradeIds.current.size > 0);
          
          if(newTradesFound.length > 0) {
              const newIds = new Set(newTradesFound.map(t => t.execId));
              setNewTradeIds(newIds);
              
              setTimeout(() => {
                  setNewTradeIds(currentIds => {
                      const updatedIds = new Set(currentIds);
                      newIds.forEach(id => updatedIds.delete(id));
                      return updatedIds;
                  });
              }, 1500);
          }

          setTrades(recentTrades);
          prevTradeIds.current = new Set(recentTrades.map(t => t.execId));
          setError(null);

        } else {
           if(isInitialLoad && recentTrades === null) {
              throw new Error('Could not load recent trades data.');
           }
        }
      } catch (e) {
        const errorMessage = e instanceof Error ? e.message : 'An unknown error occurred.';
        setError(errorMessage);
        console.error("Failed to fetch recent trades:", e);
      } finally {
        if(isInitialLoad) setIsLoading(false);
      }
    };

    fetchData(true); 
    const intervalId = setInterval(() => fetchData(false), 3000); 

    return () => clearInterval(intervalId);
  }, [symbol]);

  const renderContent = () => {
    if (isLoading) {
      return <LoadingRows />;
    }
    if (error) {
      return (
        <TableRow>
          <TableCell colSpan={3} className="text-center text-red-500 p-4">
            {error}
          </TableCell>
        </TableRow>
      );
    }
    if (trades.length > 0) {
      return trades.map((trade) => {
        const isNew = newTradeIds.has(trade.execId);
        if (!trade.qty || !trade.price || !trade.execTime) return null;
        
        return (
            <TableRow 
                key={trade.execId} 
                className={cn(
                    "text-xs font-mono transition-colors duration-1000",
                    isNew && trade.side === 'Buy' && 'bg-green-500/30',
                    isNew && trade.side === 'Sell' && 'bg-red-500/30'
                )}
            >
                <TableCell className="p-2">{formatTime(trade.execTime)}</TableCell>
                <TableCell className={`p-2 ${trade.side === 'Buy' ? 'text-chart-2' : 'text-chart-5'}`}>{parseFloat(trade.price).toFixed(2)}</TableCell>
                <TableCell className="p-2 text-right">{formatAmount(trade.qty)}</TableCell>
            </TableRow>
        );
      });
    }
    return (
        <TableRow>
          <TableCell colSpan={3} className="text-center text-muted-foreground p-4">
            No trades to display.
          </TableCell>
        </TableRow>
    );
  };

  return (
    <Card>
      <CardHeader>
        <CardTitle>Recent Trades</CardTitle>
      </CardHeader>
      <CardContent className="p-0">
        <ScrollArea className="h-[200px]">
          <Table>
            <TableHeader>
              <TableRow className="text-xs">
                <TableHead className="p-2 w-[33%]">Time</TableHead>
                <TableHead className="p-2 w-[33%]">Price (USDT)</TableHead>
                <TableHead className="p-2 text-right w-[33%]">Amount</TableHead>
              </TableRow>
            </TableHeader>
            <TableBody>
              {renderContent()}
            </TableBody>
          </Table>
        </ScrollArea>
      </CardContent>
    </Card>
  );
}
