export type IndicatorSettings = z.infer<typeof IndicatorSettings>;

export const defaultIndicatorSettings: IndicatorSettings = {
    rsi: { period: 14, overbought: 70, oversold: 30 },
    macd: { fast: 12, slow: 26, signal: 9 },
    bollingerBands: { period: 20, stdDev: 2 },
    stochastic: { period: 14, slowing: 3, overbought: 80, oversold: 20 },
    atr: { period: 14 },
    williamsR: { period: 14, overbought: -20, oversold: -80 },
    cci: { period: 20, overbought: 100, oversold: -100 },
    roc: { period: 14 },
    mfi: { period: 14, overbought: 80, oversold: 20 },
    awesomeOscillator: { fast: 5, slow: 34 },
    ichimokuCloud: { tenkan: 9, kijun: 26, senkou: 52 },
    sma: { period: 20 },
    supertrendFast: { atrPeriod: 10, multiplier: 2 },
    supertrendSlow: { atrPeriod: 20, multiplier: 3 },
};


export interface RsiResult {
  rsi: number;
}

export interface MacdResult {
  macd: number;
  signal: number;
  histogram: number;
}

export interface BollingerBandsResult {
  upper: number;
  middle: number;
  lower: number;
}

export interface StochasticResult {
  k: number;
  d: number;
}

export interface AtrResult {
  atr: number;
}

export interface ObvResult {
  obv: number;
}

export interface WilliamsRResult {
  williamsR: number;
}

export interface CciResult {
  cci: number;
}

export interface RocResult {
  roc: number;
}

export interface MfiResult {
  mfi: number;
}

export interface AwesomeOscillatorResult {
    ao: number;
}

export interface IchimokuCloudResult {
    tenkanSen: number;
    kijunSen: number;
    senkouSpanA: number;
    senkouSpanB: number;
}

export interface SmaResult {
    sma: number;
}

export interface SupertrendResult {
  supertrend: number;
  direction: 'buy' | 'sell';
  upperBand: number;
  lowerBand: number;
}

export interface IndicatorData {
  rsi: RsiResult | null;
  macd: MacdResult | null;
  bollingerBands: BollingerBandsResult | null;
  stochastic: StochasticResult | null;
  atr: AtrResult | null;
  obv: ObvResult | null;
  williamsR: WilliamsRResult | null;
  cci: CciResult | null;
  roc: RocResult | null;
  mfi: MfiResult | null;
  awesomeOscillator: AwesomeOscillatorResult | null;
  ichimokuCloud: IchimokuCloudResult | null;
  sma: SmaResult | null;
  supertrendFast: SupertrendResult | null;
  supertrendSlow: SupertrendResult | null;
}

function calculateSMA(data: number[], period: number): number[] {
  const sma: number[] = [];
  if (data.length < period) return [];
  let sum = 0;
  for (let i = 0; i < period; i++) {
    sum += data[i];
  }
  sma.push(sum / period);
  for (let i = period; i < data.length; i++) {
    sum = sum - data[i - period] + data[i];
    sma.push(sum / period);
  }
  return sma;
}

function calculateEMA(data: number[], period: number): number[] {
  if (data.length < period) return [];
  const k = 2 / (period + 1);
  const ema: number[] = [data[0]];
  for (let i = 1; i < data.length; i++) {
    ema.push(data[i] * k + ema[i - 1] * (1 - k));
  }
  return ema;
}

function calculateRSI(
  data: number[],
  period: number = 14
): RsiResult | null {
  if (data.length < period) return null;

  let gains = 0;
  let losses = 0;

  for (let i = 1; i <= period; i++) {
    const change = data[i] - data[i - 1];
    if (change > 0) {
      gains += change;
    } else {
      losses -= change;
    }
  }

  let avgGain = gains / period;
  let avgLoss = losses / period;

  for (let i = period + 1; i < data.length; i++) {
    const change = data[i] - data[i - 1];
    let currentGain = change > 0 ? change : 0;
    let currentLoss = change < 0 ? -change : 0;

    avgGain = (avgGain * (period - 1) + currentGain) / period;
    avgLoss = (avgLoss * (period - 1) + currentLoss) / period;
  }

  if (avgLoss === 0) return { rsi: 100 };

  const rs = avgGain / avgLoss;
  const rsi = 100 - (100 / (1 + rs));

  return { rsi };
}

function calculateMACD(
  data: number[],
  fastPeriod: number = 12,
  slowPeriod: number = 26,
  signalPeriod: number = 9
): MacdResult | null {
  if (data.length < slowPeriod) return null;

  const emaFast = calculateEMA(data, fastPeriod);
  const emaSlow = calculateEMA(data, slowPeriod);

  const macdLine = emaSlow.map((slow, i) => emaFast[i + slowPeriod - fastPeriod] - slow);

  if (macdLine.length < signalPeriod) return null;

  const signalLine = calculateEMA(macdLine, signalPeriod);
  const histogram = macdLine.slice(macdLine.length - signalLine.length).map((macd, i) => macd - signalLine[i]);

  return {
    macd: macdLine[macdLine.length - 1],
    signal: signalLine[signalLine.length - 1],
    histogram: histogram[histogram.length - 1],
  };
}

function calculateBollingerBands(
  data: number[],
  period: number = 20,
  stdDev: number = 2
): BollingerBandsResult | null {
  if (data.length < period) return null;

  const slice = data.slice(data.length - period);
  const sma = slice.reduce((a, b) => a + b, 0) / period;
  const standardDeviation = Math.sqrt(
    slice.map(x => Math.pow(x - sma, 2)).reduce((a, b) => a + b, 0) / period
  );

  return {
    upper: sma + stdDev * standardDeviation,
    middle: sma,
    lower: sma - stdDev * standardDeviation,
  };
}

function calculateStochastic(
  highs: number[],
  lows: number[],
  closes: number[],
  period: number = 14,
  kSlowing: number = 3
): StochasticResult | null {
  if (closes.length < period) return null;
  const kLine: number[] = [];

  for (let i = period - 1; i < closes.length; i++) {
    const highSlice = highs.slice(i - period + 1, i + 1);
    const lowSlice = lows.slice(i - period + 1, i + 1);
    const highestHigh = Math.max(...highSlice);
    const lowestLow = Math.min(...lowSlice);
    const currentClose = closes[i];
    const k = ((currentClose - lowestLow) / (highestHigh - lowestLow)) * 100;
    kLine.push(k);
  }

  if (kLine.length < kSlowing) return null;

  const dLine = calculateSMA(kLine.slice(kLine.length - kSlowing), kSlowing);

  return {
    k: kLine[kLine.length - 1],
    d: dLine[dLine.length - 1],
  };
}

function calculateTR(highs: number[], lows: number[], closes: number[]): number[] {
  const tr: number[] = [highs[0] - lows[0]];
  for (let i = 1; i < highs.length; i++) {
    tr.push(Math.max(highs[i] - lows[i], Math.abs(highs[i] - closes[i - 1]), Math.abs(lows[i] - closes[i - 1])));
  }
  return tr;
}


function calculateATR(
  highs: number[],
  lows: number[],
  closes: number[],
  period: number = 14
): AtrResult | null {
  if (closes.length < period) return null;
  const trs = calculateTR(highs, lows, closes);
  const atr = calculateSMA(trs, period);
  return atr.length > 0 ? { atr: atr[atr.length - 1] } : null;
}

function calculateOBV(
  closes: number[],
  volumes: number[],
  period: number = 14
): ObvResult | null {
  if (closes.length < 2) return null;
  let obv = 0;
  for (let i = 1; i < closes.length; i++) {
    if (closes[i] > closes[i - 1]) {
      obv += volumes[i];
    } else if (closes[i] < closes[i - 1]) {
      obv -= volumes[i];
    }
  }
  return { obv };
}

function calculateWilliamsR(
  highs: number[],
  lows: number[],
  closes: number[],
  period: number = 14
): WilliamsRResult | null {
  if (closes.length < period) return null;
  const sliceHighs = highs.slice(highs.length - period);
  const sliceLows = lows.slice(lows.length - period);
  const highestHigh = Math.max(...sliceHighs);
  const lowestLow = Math.min(...sliceLows);
  const currentClose = closes[closes.length - 1];

  const williamsR = ((highestHigh - currentClose) / (highestHigh - lowestLow)) * -100;

  return { williamsR };
}

function calculateCCI(
  highs: number[],
  lows: number[],
  closes: number[],
  period: number = 20
): CciResult | null {
  if (closes.length < period) return null;
  const typicalPrices = highs.map((h, i) => (h + lows[i] + closes[i]) / 3);
  const sma = calculateSMA(typicalPrices.slice(typicalPrices.length - period), period);
  const lastSma = sma[sma.length - 1];

  const slice = typicalPrices.slice(typicalPrices.length - period);
  const meanDeviation = slice.reduce((sum, price) => sum + Math.abs(price - lastSma), 0) / period;

  const lastTypicalPrice = typicalPrices[typicalPrices.length - 1];
  const cci = (lastTypicalPrice - lastSma) / (0.015 * meanDeviation);

  return { cci };
}

function calculateROC(
  closes: number[],
  period: number = 14
): RocResult | null {
  if (closes.length < period + 1) return null;
  const priorClose = closes[closes.length - 1 - period];
  const currentClose = closes[closes.length - 1];
  const roc = ((currentClose - priorClose) / priorClose) * 100;
  return { roc };
}

function calculateMFI(
  highs: number[],
  lows: number[],
  closes: number[],
  volumes: number[],
  period: number = 14
): MfiResult | null {
    if (closes.length < period + 1) return null;

    const typicalPrices = closes.map((c, i) => (highs[i] + lows[i] + c) / 3);

    let posMoneyFlow = 0;
    let negMoneyFlow = 0;

    for (let i = closes.length - period; i < closes.length; i++) {
        const rawMoneyFlow = typicalPrices[i] * volumes[i];
        if (typicalPrices[i] > typicalPrices[i-1]) {
            posMoneyFlow += rawMoneyFlow;
        } else if (typicalPrices[i] < typicalPrices[i-1]) {
            negMoneyFlow += rawMoneyFlow;
        }
    }
    
    if (negMoneyFlow === 0) return { mfi: 100 };
    const moneyRatio = posMoneyFlow / negMoneyFlow;
    const mfi = 100 - (100 / (1 + moneyRatio));
    return { mfi };
}


function calculateAwesomeOscillator(
    highs: number[],
    lows: number[],
    fastPeriod: number = 5,
    slowPeriod: number = 34
): AwesomeOscillatorResult | null {
    if (highs.length < slowPeriod) return null;
    const midpoints = highs.map((h, i) => (h + lows[i]) / 2);
    const smaFast = calculateSMA(midpoints, fastPeriod);
    const smaSlow = calculateSMA(midpoints, slowPeriod);
    const ao = smaFast[smaFast.length - 1] - smaSlow[smaSlow.length - slowPeriod + fastPeriod];
    return { ao };
}

function calculateIchimokuCloud(
    highs: number[],
    lows: number[],
    closes: number[],
    tenkanPeriod: number = 9,
    kijunPeriod: number = 26,
    senkouSpanBPeriod: number = 52
): IchimokuCloudResult | null {
    if (closes.length < senkouSpanBPeriod) return null;

    const tenkanSen = (Math.max(...highs.slice(highs.length - tenkanPeriod)) + Math.min(...lows.slice(lows.length - tenkanPeriod))) / 2;
    const kijunSen = (Math.max(...highs.slice(highs.length - kijunPeriod)) + Math.min(...lows.slice(lows.length - kijunPeriod))) / 2;
    
    // Senkou Span A is forward looking, but for current analysis we use current tenkan/kijun
    const senkouSpanA = (tenkanSen + kijunSen) / 2;
    
    const senkouSpanB = (Math.max(...highs.slice(highs.length - senkouSpanBPeriod)) + Math.min(...lows.slice(lows.length - senkouSpanBPeriod))) / 2;

    return { tenkanSen, kijunSen, senkouSpanA, senkouSpanB };
}

function calculateSupertrend(
  highs: number[],
  lows: number[],
  closes: number[],
  atrPeriod: number,
  multiplier: number
): SupertrendResult | null {
  if (closes.length < atrPeriod) return null;
  const tr = calculateTR(highs, lows, closes);
  const atr = calculateSMA(tr.slice(tr.length - atrPeriod), atrPeriod);
  
  if (atr.length === 0) return null;

  const currentAtr = atr[atr.length - 1];
  const close = closes[closes.length - 1];
  const prevClose = closes[closes.length - 2];

  let upperBand = (highs[highs.length - 1] + lows[lows.length - 1]) / 2 + multiplier * currentAtr;
  let lowerBand = (highs[highs.length - 1] + lows[lows.length - 1]) / 2 - multiplier * currentAtr;
  
  // This is a simplified logic for trend determination. A full implementation would carry the state.
  const direction = close > upperBand && prevClose <= upperBand ? 'buy' :
                    close < lowerBand && prevClose >= lowerBand ? 'sell' :
                    close > lowerBand ? 'buy' : 'sell';

  const supertrend = direction === 'buy' ? lowerBand : upperBand;

  return { supertrend, direction, upperBand, lowerBand };
}


export function calculateIndicators(
  closePrices: number[],
  highPrices: number[],
  lowPrices: number[],
  volumes: number[],
  settings: IndicatorSettings
): IndicatorData {
  return {
    rsi: calculateRSI(closePrices, settings.rsi.period),
    macd: calculateMACD(closePrices, settings.macd.fast, settings.macd.slow, settings.macd.signal),
    bollingerBands: calculateBollingerBands(closePrices, settings.bollingerBands.period, settings.bollingerBands.stdDev),
    stochastic: calculateStochastic(highPrices, lowPrices, closePrices, settings.stochastic.period, settings.stochastic.slowing),
    atr: calculateATR(highPrices, lowPrices, closePrices, settings.atr.period),
    obv: calculateOBV(closePrices, volumes),
    williamsR: calculateWilliamsR(highPrices, lowPrices, closePrices, settings.williamsR.period),
    cci: calculateCCI(highPrices, lowPrices, closePrices, settings.cci.period),
    roc: calculateROC(closePrices, settings.roc.period),
    mfi: calculateMFI(highPrices, lowPrices, closePrices, volumes, settings.mfi.period),
    awesomeOscillator: calculateAwesomeOscillator(highPrices, lowPrices, settings.awesomeOscillator.fast, settings.awesomeOscillator.slow),
    ichimokuCloud: calculateIchimokuCloud(highPrices, lowPrices, closePrices, settings.ichimokuCloud.tenkan, settings.ichimokuCloud.kijun, settings.ichimokuCloud.senkou),
    sma: { sma: calculateSMA(closePrices, settings.sma.period).pop() || 0 },
    supertrendFast: calculateSupertrend(highPrices, lowPrices, closePrices, settings.supertrendFast.atrPeriod, settings.supertrendFast.multiplier),
    supertrendSlow: calculateSupertrend(highPrices, lowPrices, closePrices, settings.supertrendSlow.atrPeriod, settings.supertrendSlow.multiplier)
  };
}
